const app = getApp()
import helper from '../common/helper.js';
export default {
	data() {
		return {
			userInfo: {},
			imgUrl: ''
		}
	},
	onLoad() {
		this.imgUrl = helper.imgUrl
	},
	onShow() {
		this.getUserInfo()
	},
	methods: {
		// 进入页面
		goPageNavigateTo(e) {
			console.log(e)
			let url = e.currentTarget.dataset.name
			uni.navigateTo({
				url: `/pages/${url}`
			})
		},
		getUserInfo() {
			// 用户信息获取
			// uni.showLoading({
			//   title: '用户信息获取中...'
			// });
			// uni.showLoading({
			//   title: '加载中...',
			// 	duration: 1000000
			// });
			uni.request({
				url: `${helper.requestUrl}/me`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					uni.hideLoading();
					res = helper.null2str(res)
					console.log(res,'++++++++')
					this.userInfo = res.data
					console.log(this.userInfo.mobile)
					if (res.data.status_code == 200) {
						
						
					}
				}
			})
		}
	}
}
